
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }
  
  body {
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    background-color: var(--body-color);
    color: var(--text-color);
    margin: 0;
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  
  .homhome-container {
    overflow-x: hidden;
    position: relative;
    height: 100%vh;
    background-attachment: fixed;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: flex-start; 
    align-items: center;
    transform: scale(1);
    transition: transform 0.5s ease;
    background: linear-gradient(135deg, rgb(16,16,17), rgb(15, 14, 14));
    background-size: 200% 200%;
    padding-top: 100px;
  }
  
  
  @keyframes fadeHeading {
    0%, 100% {
      opacity: 0;
      transform: translateY(20px);
    }
    50% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  
  .homhome-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  
  .homresponsive-lottie-hi {
    height: 230px;
    width: 600px;
    margin-bottom: 20px;
  }
  
  
  .homheading {
    z-index: 1;
    width: 100%; 
    text-align: center;
    margin: 0; 
    padding: 0; 
    word-wrap: break-word; /* Break long words to fit within the container */
    overflow-wrap: break-word; 
  }
  
  .homheading h1 {
    font-size: 3.3vw; 
    font-family: 'Poppins', sans-serif;
    color: rgb(255,0,0);
    margin: 0;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    animation: typing 4s steps(40, end), blink 0.75s step-end infinite;
    margin-bottom: 20px;
    margin-top: 0px; 
  }
  
  @media (max-width: 768px) {
    .homheading h1 {
      font-size: 4vw; 
    }
  }
  
  .homanimated-line {
    width: 80%; 
    height: 2px; 
    margin: 40px auto; 
    background: linear-gradient(90deg, rgba(255, 0, 0, 0) 0%, rgba(255, 0, 0, 1) 50%, rgba(255, 0, 0, 0) 100%);
    background-size: 200% 100%;
    animation: slide 2s ease-in-out infinite;
  }
  
  @keyframes slide {
    0% {
      background-position: 200% 0;
    }
    100% {
      background-position: -200% 0;
    }
  }
  
  
  .homheading p {
    font-size: 1.6rem;
    color: #b8bec4;
    line-height: 1.5; 
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-align: center;
    width: 100vw; 
    box-sizing: border-box;
    max-width: 700px;
    margin: 10px auto; 
  }
  
  @media (max-width: 480px) {
    .homheading h1 {
      font-size: 15vw; 
    }
    .homheading p {
      font-size: 21px;
      white-space: normal;
      word-break: break-word; 
      overflow-wrap: break-word; 
    }
  }
    
  
  
  .homsubheading {
    width: 100%; 
    text-align: center;
    margin: 0; 
    padding: 0; 
  }
  
  .homsubheading h2 {
    font-size: 2.5rem;
    color: rgb(248, 233, 233);
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
    margin-top: 0px; 
  }
  
  .homsubheading p {
    font-size: 1.4rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #b8bec4;
    line-height: 1.5; 
    text-align: center;
    width: 90%; 
