    margin: 10px auto; 
  }
  
  @media (max-width: 480px){
    .homsubheading p{
      font-size: 20px;
    }
  
  }
  
  .homcard-container, .homboxes {
    display: flex;
    flex-wrap: wrap;
    gap:0;
    justify-content: center;
  }
  
  .homcard {
    width: 300px;
    height: 400px;
    overflow: hidden;
    margin: 50px; 
    perspective: 1000px;
    background: linear-gradient(135deg, rgb(43, 10, 10), rgb(255, 100, 100));
    border-radius: 1rem 1rem 11rem 11rem;
    padding: 2.5rem 1.5rem 3.5rem;
    text-align: center;
    box-shadow: 0 8px 32px hsla(178, 55%, 20%, 0.15);
  }
  
  .homcontent {
    width: 100%;
    height: 100%;
    word-wrap: break-word;
    transform-style: preserve-3d;
    transition: transform 300ms;
    box-shadow: 0px 0px 15px 3px #000000ee;
    border-radius: 10px;
  }
  
  .homfront, .homback {
    background-color: #151515;
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 10px;
    overflow: hidden;
    margin: 0; 
    padding: 0; 
  }
  
  .homback {
    justify-content: center;
    display: flex;
    align-items: center;
  }
  
  .homback::before {
    position: absolute;
    content: ' ';
    display: block;
    width: 200px; 
    height: 200%; 
    animation: rotation_481 5000ms infinite linear;
  }
  
  .homback-content {
    width: 95%;
    height: 95%;
    background-color: #151515;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
  
  .homback-content h2 {
    color: #c91a13;
    font-size: 2.5rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }
  
  .homback-content strong {
    color: #d7d9db;
    font-size: 24px;
    font-family: 'Times New Roman', Times, serif;
  }
  
  .homcard:hover .homcontent {
    transform: rotateY(180deg);
    transition: 1.1s;
  }
  
  .homfront {
    transform: rotateY(180deg);
    color: #b8bec4;
    display: flex;
  }
  
  .homfront .homfront-content {
    position: absolute;
    width: 98%; 
    height: 100%;
    padding: 11px; 
    display: flex;
    flex-direction: row;
    align-items: flex-start; 
    text-wrap: pretty;
    line-height: 2;
    word-wrap: break-word;
  }
  
  .homtitle-1 {
    font-size: 17.8px;
    line-height: 1.4;
    font-family: 'Times New Roman', Times, serif;
    word-wrap: break-word;
    word-break: break-word; 
    hyphens: auto; 
  }
  
  .homtitle-2 {
    font-size: 18px;
    line-height: 1.4;
    font-family: 'Times New Roman', Times, serif;
    word-wrap: break-word;
    word-break: break-word; 
    hyphens: auto; 
  }
  
  .homtitle-3 {
    font-size: 18.6px;
    line-height: 1.4;
    font-family: 'Times New Roman', Times, serif;
    word-wrap: break-word;
    word-break: break-word; 
    hyphens: auto; 
  }
  
  @media (max-width: 480px) {
    .home-container {
      height: 400vh;
      background: linear-gradient(135deg, #151719, #151719);
    }
    .homcard {
      width: 90%;
      height: auto;
    }
  
    .homcontent {
      box-shadow: 0px 0px 10px 2px #000000cc;
    }
  
    .homfront-content {
      padding: 15px;
    }
  
    .homtitle-1 {
