    }
  
    .homtitle-2 {
      font-size: 14.5px;
    }
  
    .homtitle-3 {
      font-size: 14.5px;
    }
  }
  
  @media (max-width: 768px) {
    .home-container {
      height: 280vh;
      background: linear-gradient(135deg, #151719, #151719);
    }
    .homcard {
      width: 250px;
      height: 350px;
    }
  
    .homtitle {
      font-size: 12px;
    }
    .homtitle-1 {
